@font-face {
  font-family: "Myriad Pro Light SemiExtended";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("MyriadPro-LightSemiExt.eot");
  src: url("MyriadPro-LightSemiExt.eot?#iefix") format("embedded-opentype"), url("../fonts/Myriad-Pro-Light-SemiExtended.otf") format("opentype"); }

@font-face {
  font-family: "Myriad Pro Semibold";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("MyriadPro-Semibold.eot");
  src: url("MyriadPro-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/Myriad-Pro-Semibold.ttf") format("truetype"); }

@font-face {
  font-family: "Helvetica Neue";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("Helvetica-Neue.eot");
  src: url("Helvetica-Neue.eot?#iefix") format("embedded-opentype"), url("../fonts/Helvetica-Neue.ttf") format("truetype"); }

@font-face {
  font-family: "SFUIDisplay";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/sf-ui-display-light.ttf") format("truetype"); }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: normal; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

p {
  margin: 0; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%); }

html {
  overflow: scroll;
  overflow-x: hidden; }

::-webkit-scrollbar {
  width: 0px;
  /* remove scrollbar space */
  background: transparent;
  /* optional: just make scrollbar invisible */ }

/* optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #ff0000; }

body {
  font-family: "Myriad Pro Light SemiExtended", "Arial", sans-serif; }

.container {
  width: 320px;
  margin: 0 auto; }

.page-grid {
  display: grid;
  grid-template-columns: 320px;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "main" "footer"; }

.page-grid__header {
  grid-area: header; }

.page-grid__main {
  grid-area: main; }

.page-footer {
  grid-area: footer; }

.header {
  display: grid;
  grid-template-areas: "nav logo search phone cart";
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr;
  grid-template-rows: 1fr; }

.header__nav {
  grid-area: nav; }

.header__logo {
  grid-area: logo; }

.header__search {
  grid-area: search; }

.header__phone {
  grid-area: phone; }

.header__cart {
  grid-area: cart; }
